.attractions-child {
  position: relative;
  border-radius: 11.96px;
  width: 48.88px;
  height: 48.88px;
  flex-shrink: 0;
}
.attractions1 {
  position: relative;
  font-size: 11.21px;
  letter-spacing: 0.04em;
  font-family: var(--components-button-large);
  color: var(--color-gray-100);
  text-align: center;
  display: inline-block;
  width: 59.8px;
  height: 13.46px;
  flex-shrink: 0;
}
.attractions {
  cursor: pointer;
  border: 5px solid var(--color-green);
  padding: 0;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5.98px;
}
@media screen and (max-width: 428px) {
  .attractions {
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
    width: 33.33%;
    text-align: center;
  }
}
