.sin {
  position: relative;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  display: inline-block;
  width: 33.44px;
  height: 20.93px;
  flex-shrink: 0;
}
.from-details,
.singapore {
  width: 53.82px;
  flex-shrink: 0;
}
.singapore {
  position: relative;
  font-size: 10.47px;
  letter-spacing: 0.02em;
  text-transform: capitalize;
  color: var(--color-gray-100);
  display: inline-block;
  height: 11.96px;
}
.from-details {
  height: 34.39px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-12xs);
}
.duration-child,
.h-55m {
  position: relative;
  width: 87.72px;
  height: 17.94px;
  flex-shrink: 0;
}
.h-55m {
  letter-spacing: 0.02em;
  text-transform: capitalize;
  display: inline-block;
  width: 49.08px;
  height: 11.96px;
}
.duration {
  width: 87.72px;
  height: 32.96px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-10xs);
  font-size: 10.47px;
  color: var(--color-gray-100);
}
.lax {
  position: relative;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  display: inline-block;
  width: 38.33px;
  height: 20.93px;
  flex-shrink: 0;
}
.los-angeles,
.to-details {
  width: 65.24px;
  flex-shrink: 0;
}
.los-angeles {
  position: relative;
  font-size: 10.47px;
  letter-spacing: 0.02em;
  text-transform: capitalize;
  color: var(--color-gray-100);
  display: inline-block;
  height: 11.96px;
}
.to-details {
  height: 34.39px;
  display: flex;
  flex-direction: column;
  padding: 0.000005055463134340243px 0.000041961669921875px var(--padding-0)
    var(--padding-0);
  box-sizing: border-box;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-12xs);
  text-align: right;
}
.to-and-from {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.horizontal-line {
  align-self: stretch;
  position: relative;
  border-top: 0.7px solid var(--color-whitesmoke);
  box-sizing: border-box;
  height: 0.75px;
  flex-shrink: 0;
}
.days-to-go-container,
.departs-on-1 {
  position: relative;
  letter-spacing: 0.02em;
  display: inline-block;
  flex-shrink: 0;
}
.departs-on-1 {
  text-transform: capitalize;
  width: 152.42px;
}
.days-to-go-container {
  text-align: right;
  width: 65.21px;
}
.departure-details {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 10.47px;
  color: var(--color-gray-100);
}
.flight-main-container {
  border-radius: 5.98px;
  border: 10px solid var(--color-whitesmoke);
  display: flex;
  flex-direction: column;
  padding: 14.950791358947754px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base-7);
  text-align: left;
  font-size: 17.94px;
  color: var(--color-cornflowerblue-200);
  font-family: var(--components-button-large);
}
@media screen and (max-width: 1440px) {
  .flight-main-container {
    width: 520px;
  }
}
@media screen and (max-width: 768px) {
  .horizontal-line {
    width: 100%;
  }
  .flight-main-container {
    width: 100%;
    display: block;
    margin-bottom: 30px;
  }
}
