@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Baloo+Bhai:wght@400&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --components-button-large: Roboto;
  --font-baloo-bhai: "Baloo Bhai";

  /* font sizes */
  --font-size-xs: 12px;
  --components-button-large-size: 15px;
  --font-size-sm-5: 13.5px;
  --font-size-3xl-4: 22.4px;
  --font-size-xs-2: 11.2px;
  --font-size-base-5: 16.5px;
  --font-size-xl: 20px;
  --font-size-3xl: 22px;
  --font-size-4xl-9: 23.9px;
  --font-size-9xl-4: 28.4px;
  --font-size-2xs-5: 10.5px;
  --font-size-mid-9: 17.9px;
  --font-size-4xs: 9px;

  /* Colors */
  --primary-contrast: #fff;
  --color-orange: #f99a0e;
  --color-gray-100: #8e8e94;
  --color-gray-200: rgba(0, 0, 0, 0.23);
  --color-whitesmoke: #eaeaea;
  --color-darkslategray-100: #424242;
  --color-darkslategray-200: #303030;
  --color-cornflowerblue-100: #1262af;
  --color-cornflowerblue-200: #1262ae;
  --color-gainsboro: rgba(226, 226, 226, 0.1);
  --color-lavender: #ccdcec;
  --color-darkgray: #999;
  --color-green: #008000;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-3xl-4: 22.4px;
  --gap-5xs-5: 7.5px;
  --gap-mini: 15px;
  --gap-mid-2: 17.2px;
  --gap-7xs: 6px;
  --gap-11xs: 2px;
  --gap-4xs-2: 8.2px;
  --gap-8xs-5: 4.5px;
  --gap-mid-9: 17.9px;
  --gap-10xs-2: 2.2px;
  --gap-12xs: 1px;
  --gap-7xl-9: 26.9px;
  --gap-base-7: 15.7px;
  --gap-10xs: 3px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-5xl: 24px;
  --padding-11xl: 30px;
  --padding-0: 0px;
  --padding-mini: 15px;
  --padding-7xs: 6px;
  --padding-base-4: 16.4px;
  --padding-40xl-8: 59.8px;
  --padding-4xs: 9px;
  --padding-xs-2: 11.2px;
  --padding-3xs-7: 9.7px;
  --padding-xs: 12px;
  --padding-base: 16px;
  --padding-xl: 20px;
  --padding-9xs-7: 3.7px;

  /* border radiuses */
  --br-10xs: 3px;
  --br-4xl-2: 23.2px;
  --br-5xs-5: 7.5px;
  --br-xs: 12px;
  --br-7xs: 6px;
}
